<template>
    <!--首页附近商家-->
    <div class="msite_shop_list">
        <div class="shop_header">
            <i class="iconfont icon-xuanxiang"></i>
            <span class="shop_header_title">附近商家</span>
        </div>
        <div class="shop_container">
            <ul class="shop_list">
                <li class="shop_li border-1px" v-for="shop in shops"
                    :key="shop.id" @click="$router.replace(`/Shop/${shop.id}`)">
                    <a>
                        <div class="shop_left">
                            <img class="shop_img" :src="baseImgUrl + shop.image_path">
                        </div>
                        <div class="shop_right">
                            <section class="shop_detail_header">
                                <h4 class="shop_title ellipsis">{{shop.name}}</h4>
                                <ul class="shop_detail_ul">
                                    <li v-for="support in shop.supports" class="supports">{{support.icon_name}}</li>
                                </ul>
                            </section>
                            <section class="shop_rating_order">
                                <section class="shop_rating_order_left">
                                    <stars size="24" :score="shop.rating" :marginR="2"
                                           class="stars"></stars>
                                    <div class="rating_section">
                                        {{shop.rating}}
                                    </div>
                                    <div class="order_section">
                                        月售{{shop.recent_order_num}}单
                                    </div>
                                </section>
                                <section class="shop_rating_order_right">
                                    <span class="delivery_style delivery_right" v-if="shop.delivery_mode">
                                        {{shop.delivery_mode.text}}
                                    </span>
                                </section>
                            </section>
                            <section class="shop_distance">
                                <p class="shop_delivery_msg">
                                    <span>¥20起送</span>
                                    <span class="segmentation">/</span>
                                    <span>配送费约¥5</span>
                                </p>
                            </section>
                        </div>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import stars from "components/ele-stars/ele-stars.vue"
    import {mapState,mapActions} from "vuex"
    import BScroll from "better-scroll"
    export default {
        name: "ShopList",
        methods:{
            ...mapActions(["getShops"])
        },
        computed:{
            // ...mapState(["shops","baseImgUrl"])
            ...mapState({
                shops: state => state.msite.shops,
                baseImgUrl: state => state.msite.baseImgUrl
            })
        },
        components:{
            stars
        },
        async mounted(){
            await this.getShops()
            new BScroll(".shop_container",{
                click:true
            })
        }
    }
</script>

<style scoped lang="stylus">
    @import "../../common/stylus/mixins.styl"
    .msite_shop_list
        top-border-1px(#e4e4e4)
        position absolute
        left 0
        right 0
        top 245px
        bottom 50px
        background #fff
        margin-top 10px
        .shop_header
            padding 10px 10px 0
            .shop_icon
                margin-left 5px
                color #999
            .shop_header_title
                color #999
                font-size 14px
                line-height 20px
        .shop_container
            position absolute
            top 30px
            bottom 0
            left 0
            right 0
            overflow hidden
            .shop_list
                .shop_li
                    bottom-border-1px(#f1f1f1)
                    width 100%
                    >a
                        clearFix()
                        display block
                        box-sizing border-box
                        padding 15px 8px
                        width 100%
                        .shop_left
                            float left
                            box-sizing border-box
                            width 23%
                            height 75px
                            padding-right 10px
                            .shop_img
                                display block
                                width 100%
                                height 100%
                        .shop_right
                            float right
                            width 77%
                            .shop_detail_header
                                clearFix()
                                width 100%
                                .shop_title
                                    float left
                                    width 200px
                                    color #333
                                    font-size 16px
                                    line-height 16px
                                    font-weight 700
                                    &::before
                                        content '品牌'
                                        display inline-block
                                        font-size 11px
                                        line-height 11px
                                        color #333
                                        background-color #ffd930
                                        padding 2px 2px
                                        border-radius 2px
                                        margin-right 5px
                                .shop_detail_ul
                                    float right
                                    margin-top 3px
                                    .supports
                                        float left
                                        font-size 10px
                                        color #999
                                        border 1px solid #f1f1f1
                                        padding 0 2px
                                        border-radius 2px
                            .shop_rating_order
                                clearFix()
                                width 100%
                                margin-top 18px
                                margin-bottom 8px
                                .shop_rating_order_left
                                    float left
                                    color #ff9a0d
                                    .stars //2x图 3x图
                                        float left
                                        font-size 0
                                    .rating_section
                                        float left
                                        font-size 10px
                                        color #ff6000
                                        margin-left 4px
                                    .order_section
                                        float left
                                        font-size 10px
                                        color #666
                                        transform scale(.8)
                                .shop_rating_order_right
                                    float right
                                    font-size 0
                                    .delivery_style
                                        transform-origin 35px 0
                                        transform scale(.7)
                                        display inline-block
                                        font-size 12px
                                        padding 1px
                                        border-radius 2px
                                    .delivery_left
                                        color #fff
                                        margin-right -10px
                                        background-color #02a774
                                        border 1px solid #02a774
                                    .delivery_right
                                        color #02a774
                                        border 1px solid #02a774
                            .shop_distance
                                clearFix()
                                width 100%
                                font-size 12px
                                .shop_delivery_msg
                                    float left
                                    transform-origin 0
                                    transform scale(.9)
                                    color #666
                                .segmentation
                                    color #ccc
</style>